import axios from "axios";
import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  DocumentAttributesInterface,
  DocumentCategoryInterface,
  DocumentFieldsInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import {
  createDocumentFields,
  updateDocumentFields,
} from "../../actions/document";
import { errorToText } from "../../utils/errors";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PopupAlertData } from "../../components/Alert/Popup";
import { isEmptyOrSpaces } from "../../shared/dataTest";

interface AppProps {
  thisCategory: DocumentCategoryInterface;
  subCategoryId: string;
  docFieldsId: string;
  docFieldsTitle: string;
  docFieldsDescription: string;
  documentCategory: DocumentCategoryInterface[];
  documentSubCategory: DocumentSubCategoryInterface[];
  viewFields: "" | "SUB_CATEGORY" | "CATEGORY";
  actionType: string;
  setDocFieldsId: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldsTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldsDescription: React.Dispatch<React.SetStateAction<string>>;
  setActionType: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
    >
  >;
  createDocumentFields: Function;
  updateDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const CreateDocumentField: React.FC<AppProps> = ({
  thisCategory,
  subCategoryId,
  docFieldsId,
  docFieldsTitle,
  docFieldsDescription,
  viewFields,
  actionType,
  documentCategory,
  documentSubCategory,
  setDocFieldsId,
  setDocFieldsTitle,
  setDocFieldsDescription,
  setActionType,
  createDocumentFields,
  updateDocumentFields,
  PushPopupAlert,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const [categoryId, setCategoryId] = useState<string>("");
  const [subCategoryID, setSubCategoryID] = useState<string>("");
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);

  useEffect(() => {
    if (!categoryId.length && thisCategory) {
      let data: DocumentCategoryInterface[] = documentCategory.filter(
        (element) =>
          element.user_detail_category_id ===
          thisCategory.user_detail_category_id
      );
      if (data.length > 0) {
        if (
          data[0].fields.includes(
            DocumentAttributesInterface.category_subcategory_data
          )
        ) {
          setCategoryId(thisCategory.user_detail_category_id);
        }
      }
    }
    if (!subCategoryID.length && subCategoryId) {
      setSubCategoryID(subCategoryId);
    }
  }, [
    categoryId.length,
    thisCategory,
    documentCategory,
    subCategoryID.length,
    subCategoryId,
  ]);

  const isFieldRequired = (category_id: string): boolean => {
    let data: DocumentCategoryInterface[] = documentCategory.filter(
      (element) => element.user_detail_category_id === category_id
    );
    if (data.length > 0) {
      if (
        data[0].fields.includes(
          DocumentAttributesInterface.category_subcategory_data
        )
      )
        return true;
      else return false;
    } else {
      return false;
    }
  };

  const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    console.log({
      category_id: categoryId,
      subcategory_id:
        viewFields === "SUB_CATEGORY"
          ? subCategoryID === "null"
            ? null
            : subCategoryID
          : subCategoryID,
      title: docFieldsTitle,
      description: docFieldsDescription,
      others: [],
    });
    if (!categoryId.length || isEmptyOrSpaces(categoryId)) {
      return setFormError({
        element: "doc_category",
        msg: "Please select a category",
      });
    }

    if (
      (!subCategoryID.length || isEmptyOrSpaces(subCategoryID)) &&
      isFieldRequired(categoryId)
    ) {
      return setFormError({
        element: "doc_sub_category",
        msg: "Please select a sub category",
      });
    }

    if (!docFieldsTitle.length) {
      return setFormError({
        element: "field_title",
        msg: "Please enter Field Title",
      });
    }

    if (!docFieldsDescription.length) {
      return setFormError({
        element: "field_desc",
        msg: "Please enter Field Description",
      });
    }

    setLoadind(true);
    console.log({
      category_id: categoryId,
      subcategory_id: subCategoryID === "null" ? null : subCategoryID,
      title: docFieldsTitle,
      description: docFieldsDescription,
      others: [],
    });
    try {
      if (actionType === "CREATE_FIELD") {
        const res = await axios.post<{
          data: {
            category_subcategory_Data_id: string;
            category_id: string;
            subcategory_id: string | null;
            title: string;
            description: string;
            others: [];
          };
        }>(`${API.AUTH}/categorysubcatedata`, {
          category_id: thisCategory.user_detail_category_id,
          subcategory_id: subCategoryID === "null" ? null : subCategoryID,
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        });
        console.log({ res });
        createDocumentFields({
          _id: res.data.data.category_subcategory_Data_id,
          category_id: res.data.data.category_id,
          subcategory_id: res.data.data.subcategory_id,
          title: res.data.data.title,
          description: res.data.data.description,
          others: res.data.data.others,
        });
        PushPopupAlert({
          title: `${
            viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
          }'s Field has been created!`,
          theme: PopupAlertTheme.success,
        });
        setDocFieldsTitle("");
        setDocFieldsDescription("");
      } else if (actionType === "UPDATE_FIELD") {
        const formData: {
          category_subcategory_data_id: string;
          category_id: string;
          subcategory_id: string | null;
          title: string;
          description: string;
          others: [];
        } = {
          category_subcategory_data_id: docFieldsId,
          category_id: categoryId,
          subcategory_id: subCategoryID === "null" ? null : subCategoryID,
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        };
        const reducerData: DocumentFieldsInterface = {
          _id: docFieldsId,
          category_id: categoryId,
          subcategory_id: subCategoryID === "null" ? null : subCategoryID,
          title: docFieldsTitle,
          description: docFieldsDescription,
          others: [],
        };
        const res = await axios.patch<{ data: DocumentFieldsInterface }>(
          `${API.AUTH}/categorysubcatedata`,
          formData
        );
        if (res.data.data) {
          console.log({ updated: res.data.data });
          updateDocumentFields(reducerData);
          PushPopupAlert({
            title: `${
              viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
            }'s Field has been updated!`,
            theme: PopupAlertTheme.success,
          });
          setDocFieldsId("");
          setDocFieldsTitle("");
          setDocFieldsDescription("");
          setActionType("");
          setLoadind(false);
        }
      }
    } catch (error) {
      console.error({ doc_SUB_category_error: error });
      PushPopupAlert({
        title: "Updating Field",
        description: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
    }
    setLoadind(false);
  };

  return (
    <form onSubmit={(e) => handleSubmit(e)}>
      {console.log({ actionType })}
      <div className="divide-y divide-gray-200 px-6">
        <div className="my-4 text-base text-gray-700 sm:text-lg">
          <div className="flex flex-col">
            <label>Category</label>
            <select
              className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                formError && formError.element === "doc_category"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                setCategoryId(e.target.value);
                setSubCategoryID("");
                formError &&
                  formError.element === "doc_category" &&
                  setFormError(null);
              }}
              value={categoryId}
              disabled={loading}
            >
              <option value="">Document Category</option>
              {documentCategory
                .filter((element) =>
                  element.fields.includes(
                    DocumentAttributesInterface.category_subcategory_data
                  )
                )
                .map((item, index) => (
                  <option
                    key={index + item.user_detail_category_id}
                    value={item.user_detail_category_id}
                  >
                    {item.title}
                  </option>
                ))}
            </select>
            {formError && formError.element === "doc_category" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col">
            <label>Sub Category</label>
            <select
              className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                formError && formError.element === "doc_sub_category"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                setSubCategoryID(e.target.value);
                formError &&
                  formError.element === "doc_sub_category" &&
                  setFormError(null);
              }}
              disabled={loading}
              value={subCategoryID}
            >
              <option value="">Document Sub Category</option>
              {isFieldRequired(categoryId) && <option value="null">All</option>}
              {documentSubCategory
                .filter((element) => element.category_id === categoryId)
                .map((item, index) => (
                  <option
                    key={index + item.user_detail_subcategory_id}
                    value={item.user_detail_subcategory_id}
                  >
                    {item.title}
                  </option>
                ))}
            </select>
            {formError && formError.element === "doc_sub_category" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col">
            <label>Title</label>
            <input
              type="text"
              className={`text-left input-white w-20 inline-block ${
                formError && formError.element === "field_title"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              placeholder="Sub Category Title"
              value={docFieldsTitle}
              onChange={(e) => {
                setDocFieldsTitle(e.target.value);
                formError &&
                  formError.element === "field_title" &&
                  setFormError(null);
              }}
              disabled={loading}
            />
            {formError && formError.element === "field_title" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
          <div className="flex flex-col">
            <label>Description</label>
            <textarea
              className={`text-left input-white w-20 inline-block ${
                formError && formError.element === "field_desc"
                  ? "border-red-500"
                  : "border-gray-500"
              } border-2`}
              placeholder="Sub Category Description"
              style={{ resize: "none" }}
              value={docFieldsDescription}
              onChange={(e) => {
                setDocFieldsDescription(e.target.value);
                formError &&
                  formError.element === "field_desc" &&
                  setFormError(null);
              }}
              rows={2}
              disabled={loading}
            ></textarea>
            {formError && formError.element === "field_desc" && (
              <span className="text-red-500">{formError.msg}</span>
            )}
          </div>
        </div>
        <div className="pt-4 flex justify-between">
          <button
            onClick={() => {
              setActionType("");
              setDocFieldsId("");
              setDocFieldsTitle("");
              setDocFieldsDescription("");
            }}
            className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
              loading && "cursor-not-allowed"
            }`}
            type="button"
            disabled={loading}
          >
            Exit
          </button>
          <button
            className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
              loading && "cursor-wait"
            }`}
            type="submit"
            disabled={loading}
          >
            {loading
              ? "Loading..."
              : actionType === "CREATE_FIELD"
              ? "Create"
              : "Save Change"}
          </button>
        </div>
      </div>
    </form>
  );
};

export default connect(null, {
  createDocumentFields,
  updateDocumentFields,
  PushPopupAlert,
})(CreateDocumentField);
