import React, { Fragment, useState } from "react";
import { connect } from "react-redux";
import { MdAdd, MdCreate, MdDelete } from "react-icons/md";
import { DocumentFieldsInterface } from "../../shared/interfaces";
import { BiDetail } from "react-icons/bi";
import { IoMdArrowRoundBack } from "react-icons/io";
import axios from "axios";
import { API } from "../../utils/api";
import { deleteDocumentFields } from "../../actions/document";
import { PushPopupAlert } from "../../actions/system";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { errorToText } from "../../utils/errors";

interface AppProps {
  subCategoryId: string;
  categoryId: string;
  thisTitle: string;
  actionType: string;
  viewFields: "" | "SUB_CATEGORY" | "CATEGORY";
  documentFields: DocumentFieldsInterface[] | null;
  setDocFieldsId: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldsTitle: React.Dispatch<React.SetStateAction<string>>;
  setDocFieldsDescription: React.Dispatch<React.SetStateAction<string>>;
  setViewFields: React.Dispatch<
    React.SetStateAction<"" | "SUB_CATEGORY" | "CATEGORY">
  >;
  setActionType: React.Dispatch<
    React.SetStateAction<
      | ""
      | "CREATE_SUB"
      | "CREATE_CAT"
      | "UPDATE_SUB"
      | "UPDATE_CAT"
      | "CREATE_FIELD"
      | "UPDATE_FIELD"
    >
  >;
  deleteDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

const Fields: React.FC<AppProps> = ({
  categoryId,
  subCategoryId,
  thisTitle,
  actionType,
  viewFields,
  documentFields,
  setDocFieldsId,
  setDocFieldsTitle,
  setDocFieldsDescription,
  setViewFields,
  setActionType,
  deleteDocumentFields,
  PushPopupAlert,
}) => {
  const [loading, setLoadind] = useState<boolean>(false);
  const handleDelete = async (fieldId: string) => {
    setLoadind(true);
    try {
      const res = await axios.delete<DocumentFieldsInterface>(
        `${API.AUTH}/categorysubcatedata/${fieldId}`
      );
      if (res.data) {
        deleteDocumentFields(fieldId);
        PushPopupAlert({
          title: `${
            viewFields === "SUB_CATEGORY" ? "Sub Category" : "Category"
          }'s Field has been deleted successfully!`,
          theme: PopupAlertTheme.success,
        });
        setLoadind(false);
      }
    } catch (error) {
      console.log({ doc_SUB_category_error: error });
      PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.danger,
      });
      setLoadind(false);
    }
  };
  return (
    <Fragment>
      {/* user details about */}
      <div className="border-b px-2 py-3 flex justify-between">
        <div className="flex items-center gap-2">
          <button
            className={`btn m-1 bg-gray-200 text-gray-700 focus:outline-none flex items-center gap-1`}
            onClick={() => setViewFields("")}
          >
            <IoMdArrowRoundBack className="w-5 h-5" />
            <span className="text-base">Back</span>
          </button>
          <h2 className="text-2xl font-semibold text-gray-800">
            {thisTitle}'s Fields
          </h2>
        </div>
        <button
          type="button"
          className="btn mb-1 border-2 border-blue-500 bg-blue-500 hover:bg-white
        text-white flex items-center font-bold hover:text-gray-700 focus:outline-none"
          onClick={() => setActionType("CREATE_FIELD")}
          disabled={actionType !== ""}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" /> Fields
        </button>
      </div>
      {console.log({ subCategoryId })}
      {documentFields &&
      documentFields.filter((data) =>
        viewFields === "SUB_CATEGORY"
          ? data.category_id === categoryId &&
            (data.subcategory_id === null ||
              data.subcategory_id === subCategoryId)
          : viewFields === "CATEGORY" && data.category_id === categoryId
      ).length > 0 ? (
        documentFields
          .filter((data) =>
            viewFields === "SUB_CATEGORY"
              ? data.category_id === categoryId &&
                (data.subcategory_id === null ||
                  data.subcategory_id === subCategoryId)
              : viewFields === "CATEGORY" && data.category_id === categoryId
          )
          .map((field, index) => (
            <div
              key={index + field._id}
              className="flex justify-between items-center p-2 my-3 rounded-lg border border-gray-100 shadow-md"
            >
              <div className="flex items-center">
                <div className="ml-2">
                  <div className="text-xl font-semibold text-gray-600">
                    {field.title}
                  </div>
                  <div className="text-sm font-light text-gray-500">
                    {field.description}
                  </div>
                </div>
              </div>

              <div className="flex items-center gap-2">
                <button
                  title="Update Field"
                  className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                    loading && "cursor-not-allowed"
                  }`}
                  onClick={() => {
                    setActionType("UPDATE_FIELD");
                    setDocFieldsTitle(field.title);
                    setDocFieldsDescription(field.description);
                    setDocFieldsId(field._id);
                  }}
                >
                  <MdCreate className="text-3xl text-blue-700" />
                </button>
                <button
                  title="Delete Field"
                  className={`bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                    loading && "cursor-wait"
                  }`}
                  onClick={() => handleDelete(field._id)}
                  disabled={loading}
                >
                  <MdDelete className="text-3xl text-red-700" />
                </button>
              </div>
            </div>
          ))
      ) : (
        <div className="text-center text-4xl text-gray-500 font-medium py-8">
          <BiDetail className="w-40 h-40 mx-auto animate__pulse" />
          No Data Found
        </div>
      )}
    </Fragment>
  );
};

export default connect(null, { deleteDocumentFields, PushPopupAlert })(Fields);
