import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import {
  DocumentCategoryInterface,
  DocumentAttributesInterface,
} from "../../shared/interfaces";
import {
  createDocumentCategory,
  updateDocumentCategory,
} from "../../actions/document";
import CheckBox from "./CheckBox";

interface AppProps {
  thisCategory: DocumentCategoryInterface | null;
  actionType: string;
  loading: boolean;
  error: {
    element: string;
    msg: string;
  };
  categoryAttributes: DocumentAttributesInterface[];
  setCategoryId: React.Dispatch<React.SetStateAction<string>>;
  setCategoryTitle: React.Dispatch<React.SetStateAction<string>>;
  setCategoryDescription: React.Dispatch<React.SetStateAction<string>>;
  setCategoryAttributes: React.Dispatch<
    React.SetStateAction<DocumentAttributesInterface[]>
  >;
  setError: React.Dispatch<
    React.SetStateAction<{
      element: string;
      msg: string;
    }>
  >;
}
const CreateStepTwo: React.FC<AppProps> = ({
  thisCategory,
  categoryAttributes,
  actionType,
  loading,
  error,
  setCategoryId,
  setCategoryTitle,
  setCategoryDescription,
  setCategoryAttributes,
  setError,
}) => {
  const [formLoaded, setFormLoaded] = useState(false);
  useEffect(() => {
    if (thisCategory && actionType === "UPDATE_CAT" && !formLoaded) {
      setCategoryId(thisCategory.user_detail_category_id);
      setCategoryTitle(thisCategory.title);
      setCategoryDescription(thisCategory.description);
      setCategoryAttributes(thisCategory.fields);
      setFormLoaded(true);
    }
  }, [
    thisCategory,
    actionType,
    formLoaded,
    setCategoryId,
    setCategoryTitle,
    setCategoryDescription,
    setCategoryAttributes,
  ]);

  const updateCategoryValue = (
    value: DocumentAttributesInterface,
    isAvailable: boolean
  ) => {
    // Destructure current state array
    const arrayvalue: DocumentAttributesInterface[] = [...categoryAttributes];
    if (isAvailable) {
      var index = arrayvalue.indexOf(value);
      if (index !== -1) {
        arrayvalue.splice(index, 1);
      }
    } else {
      arrayvalue.push(value);
    }
    console.log({ arrayvalue });
    setCategoryAttributes(arrayvalue);
  };

  return (
    <div className="text-base leading-6 space-y-4 text-gray-700 sm:text-lg sm:leading-7 mb-3 animate__animated animate__zoomIn">
      <h1 className="text-3xl font-semibold mt-3 mb-2 text-center">
        Attributes settings
      </h1>
      <div className="flex flex-col px-1 md:px-3">
        <label>Attributes</label>
        <span className="text-gray-500 text-sm">
          Required Attributes for this document
        </span>

        <div className="grid grid-flow-row grid-cols-3 grid-rows- gap-4">
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.Title}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Title</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.Description}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Description</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.Files}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Files</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.email}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Email</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.names}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Names</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.phone_number}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Phone</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.position}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Position</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.relation}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Relation</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.src}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">SRC</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.year}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Year</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.company}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Company</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.others}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Others</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.start_year}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Start Year</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.end_year}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">End Year</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.School}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">School</span>
          </label>
          <label className="inline-flex items-center mt-3">
            <CheckBox
              categoryAttributes={categoryAttributes}
              attribute={DocumentAttributesInterface.category_subcategory_data}
              error={error}
              updateCategoryValue={updateCategoryValue}
              setError={setError}
            />
            <span className="ml-2 text-gray-700">Category Data</span>
          </label>
        </div>
        {error.element === "category_attributes" ? (
          <p className="text-red-700 mt-2 text-base animate__animated animate__bounce">
            {error.msg}
          </p>
        ) : null}
      </div>
    </div>
  );
};

export default connect(null, {
  createDocumentCategory,
  updateDocumentCategory,
})(CreateStepTwo);
