import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { IoMdEye } from "react-icons/io";
import {
  MdAdd,
  MdClear,
  MdCreate,
  MdSettings,
  MdViewList,
} from "react-icons/md";
import { RiFolderSettingsFill } from "react-icons/ri";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../actions";
import {
  getDocumentCategory,
  getDocumentSubCategory,
  deleteDocumentCategory,
  getDocumentFields,
} from "../../actions/document";
import { PopupAlertData } from "../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
import {
  MainItem,
  SideNavigation,
} from "../../components/Navigation/SideNavigation";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { StoreState } from "../../reducers";
import { DocumentReducerInterface } from "../../reducers/document";
import {
  DocumentCategoryInterface,
  DocumentAttributesInterface,
  DocumentSubCategoryInterface,
} from "../../shared/interfaces";
import { API } from "../../utils/api";
import { errorToText } from "../../utils/errors";
import CreateDocument from "./CreateDocument";
import CreateDocumentField from "./CreateDocumentField";
import CreateSubDocument from "./CreateSubDocument";
import Fields from "./Fields";
import SubCategory from "./SubCategory";
interface AppProps {
  document: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  deleteDocumentCategory: Function;
  getDocumentFields: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}
const Index: React.FC<AppProps> = ({
  document,
  getDocumentCategory,
  getDocumentSubCategory,
  deleteDocumentCategory,
  getDocumentFields,
  PushPopupAlert,
}) => {
  const [docCategory, setDocCategory] =
    useState<DocumentCategoryInterface | null>(null);
  const [categoryId, setCategoryId] = useState<string>("");
  const [categoryTitle, setCategoryTitle] = useState<string>("");
  const [categoryDescription, setCategoryDescription] = useState<string>("");
  const [categoryAttributes, setCategoryAttributes] = useState<
    DocumentAttributesInterface[]
  >([]);
  const [subCategoryId, setSubCategoryId] = useState<string>("");
  const [subCategoryTitle, setSubCategoryTitle] = useState<string>("");
  const [subCategoryDescription, setSubCategoryDescription] =
    useState<string>("");
  const [docFieldsId, setDocFieldsId] = useState<string>("");
  const [docFieldsTitle, setDocFieldsTitle] = useState<string>("");
  const [docFieldsDescription, setDocFieldsDescription] = useState<string>("");
  const [viewFields, setViewFields] = useState<
    "" | "SUB_CATEGORY" | "CATEGORY"
  >("");
  const [actionType, setActionType] = useState<
    | ""
    | "CREATE_SUB"
    | "CREATE_CAT"
    | "UPDATE_SUB"
    | "UPDATE_CAT"
    | "CREATE_FIELD"
    | "UPDATE_FIELD"
  >("");
  const [loading, setLoadind] = useState<boolean>(false);
  const [nullId, setNullId] = useState<string>("");
  const [loadingPage, setLoadindPage] = useState<boolean>(true);
  const [fieldList, setFieldList] = useState<
    | {
        category_id: string;
        subcategory_id: string | null;
        title: string;
        description: string;
        others: [];
      }[]
  >([]);

  useEffect(() => {
    if (!document.documentCategory) {
      getDocumentCategory();
    } else {
      setLoadindPage(false);
    }
    if (!document.documentSubCategory) {
      getDocumentSubCategory();
    }
    if (!document.documentFields) {
      getDocumentFields();
    }
    console.log("test effect");
  }, [
    document.documentCategory,
    getDocumentCategory,
    document.documentFields,
    document.documentSubCategory,
    getDocumentFields,
    getDocumentSubCategory,
  ]);

  const handleDelete = async (categoryId: string) => {
    if (window.confirm("Are you sure do you want to delete this category?")) {
      setLoadind(true);
      try {
        const res = await axios.delete<DocumentCategoryInterface>(
          `${API.AUTH}/detailcategory/${categoryId}`
        );
        if (res.data) {
          deleteDocumentCategory(categoryId);
          PushPopupAlert({
            title: "Category has been deleted successfully.",
            theme: PopupAlertTheme.success,
          });
          setDocCategory(
            document.documentCategory ? document.documentCategory[0] : null
          );
        }
      } catch (error) {
        console.log({ doc_category_error: error });
        PushPopupAlert({
          title: errorToText(error),
          theme: PopupAlertTheme.danger,
        });
      }
      setLoadind(false);
    }
  };

  const isSubExit = (
    sub_category: DocumentSubCategoryInterface[],
    category_id: string
  ): boolean => {
    return sub_category.some((c) => c.category_id === category_id);
  };

  return (
    <Fragment>
      <SideNavigation>
        <button
          className="btn mb-3 border-2 border-blue-500 hover:bg-blue-500 hover:text-white flex items-center flex-1 font-bold w-full focus:outline-none"
          onClick={() => setActionType("CREATE_CAT")}
          disabled={actionType === "CREATE_CAT" || actionType === "UPDATE_CAT"}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" />
          <span>Category</span>
        </button>

        <button
          className="btn mb-3 border-2 border-blue-500 hover:bg-blue-500 hover:text-white flex items-center flex-1 font-bold w-full focus:outline-none"
          onClick={() => setActionType("CREATE_FIELD")}
          disabled={
            actionType === "CREATE_FIELD" || actionType === "UPDATE_FIELD"
          }
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" />
          <span>Fields</span>
        </button>

        <span className="text-base mt-2 font-semibold flex items-center">
          <MdViewList className="w-5 h-5" /> Doc Cateogries
        </span>
        {document.documentCategory && document.documentCategory.length > 0 ? (
          document.documentCategory.map((category, index) => (
            <section
              key={index + category.user_detail_category_id}
              className="mt-2"
              onClick={() => {
                setDocCategory(category);
                setCategoryId("");
                setCategoryTitle("");
                setCategoryDescription("");
                setSubCategoryId("");
                setSubCategoryTitle("");
                setViewFields("");
              }}
            >
              {index === 0 && docCategory === null && setDocCategory(category)}
              <MainItem
                title={category.title}
                active={
                  docCategory &&
                  docCategory.user_detail_category_id ===
                    category.user_detail_category_id
                    ? true
                    : false
                }
              />
            </section>
          ))
        ) : (
          <div className="border border- p-2">No category</div>
        )}
      </SideNavigation>

      <div className="w-full my-5">
        {/* header title */}

        {docCategory && (
          <div className="flex flex-row items-start gap-2">
            <RiFolderSettingsFill className="text-blue-700 w-24 h-24" />
            <div className="flex justify-between w-full mt-2">
              <div className="flex flex-col">
                <h1 className="text-3xl font-semibold">
                  {docCategory.title}'s document category
                </h1>

                <div className="flex flex-row items-center md:gap-3 gap-2">
                  <div className="flex flex-row items-center mt-1 gap-1">
                    <span className="text-gray-600 text-lg">Desc:</span>
                    <span className="text-gray-600 text-lg font-bold">
                      {docCategory.description}
                    </span>
                    {/* <MdKeyboardArrowRight />
                  <span className="text-blue-600 text-lg font-bold">
                    Room Alpha
                  </span> */}
                  </div>
                </div>
                <div className="flex gap-2 mt-1">
                  {docCategory.fields.includes(
                    DocumentAttributesInterface.category_subcategory_data
                  ) &&
                    document.documentSubCategory &&
                    !isSubExit(
                      document.documentSubCategory,
                      docCategory.user_detail_category_id
                    ) && (
                      <button
                        title={
                          viewFields === "CATEGORY"
                            ? "View Sub Category"
                            : "View Fields"
                        }
                        className={`bg-gray-600 hover:bg-gray-500" py-1 px-2 rounded shadow-md text-white focus:outline-none`}
                        onClick={() => {
                          viewFields === "CATEGORY"
                            ? setViewFields("")
                            : setViewFields("CATEGORY");
                        }}
                      >
                        {viewFields === "CATEGORY" ? (
                          <div className="flex justify-center items-center">
                            <IoMdEye className="text-white w-4 h-4 mr-1" /> Sub
                            Category
                          </div>
                        ) : (
                          <div className="flex justify-center items-center">
                            <IoMdEye className="text-white w-4 h-4 mr-1" />{" "}
                            Fields
                          </div>
                        )}
                      </button>
                    )}
                  <button
                    className="btn bg-blue-600 hover:bg-blue-500 flex justify-center items-center text-white focus:outline-none gap-1"
                    onClick={() => {
                      setCategoryId(docCategory.user_detail_category_id);
                      setCategoryTitle(docCategory.title);
                      setCategoryDescription(docCategory.description);
                      setCategoryAttributes(docCategory.fields);
                      setFieldList(
                        document.documentFields
                          ? document.documentFields.filter(
                              (field) =>
                                field.category_id ===
                                docCategory.user_detail_category_id
                            )
                          : []
                      );
                      setActionType("UPDATE_CAT");
                    }}
                  >
                    <MdCreate className="w-4 h-4" />
                    Edit
                  </button>
                  <button
                    className="btn bg-red-600 hover:bg-red-500 flex justify-center items-center text-white font-semibold focus:outline-none gap-1"
                    onClick={() =>
                      handleDelete(docCategory.user_detail_category_id)
                    }
                    disabled={loading}
                  >
                    <MdClear className="w-4 h-4" />
                    Delete
                  </button>
                </div>
              </div>
              {/* <div className="text-right flex flex-col">
              <span className="text-gray-700">Nyarugenge District</span>
              <span className="text-gray-700">Center of Nyakabanda</span>
            </div> */}
            </div>
          </div>
        )}
        {/* end header title */}
        {loadingPage ? (
          <div className="text-center py-5 text-2xl">Loading...</div>
        ) : (
          <div className="container mx-auto my-4">
            <div className="w-full flex h-full flex-col md:flex-row md:items-start gap-2">
              {/* <!-- Left Side --> */}
              <div
                className={`bg-white h-full ${
                  viewFields !== "" ? "hidden" : "block"
                } w-full p-2 border border-gray-300 rounded shadow`}
              >
                {docCategory ? (
                  <SubCategory
                    thisCategory={docCategory}
                    setNullId={setNullId}
                    actionType={actionType}
                    subCategoryId={subCategoryId}
                    documentSubCategory={document.documentSubCategory}
                    viewFields={viewFields}
                    setViewFields={setViewFields}
                    setSubCategoryId={setSubCategoryId}
                    setSubCategoryTitle={setSubCategoryTitle}
                    setSubCategoryDescription={setSubCategoryDescription}
                    setActionType={setActionType}
                  />
                ) : (
                  <div className="flex flex-col items-center self-center my-12">
                    <MdSettings className="w-16 h-16" />
                    <p>Select Category to View Sub Categories</p>
                  </div>
                )}
              </div>
              {/* <!-- End of Left Side --> */}

              {/* <!-- Right Side --> */}
              {(viewFields === "SUB_CATEGORY" || viewFields === "CATEGORY") && (
                <div
                  className={`bg-white h-full ${
                    viewFields === "SUB_CATEGORY" ? "w-full" : "w-full"
                  } p-2 border border-gray-300 rounded shadow`}
                >
                  {docCategory || subCategoryId ? (
                    <Fields
                      subCategoryId={subCategoryId}
                      categoryId={
                        docCategory
                          ? docCategory.user_detail_category_id
                          : nullId
                      }
                      thisTitle={
                        subCategoryTitle
                          ? subCategoryTitle
                          : docCategory
                          ? docCategory.title
                          : ""
                      }
                      documentFields={document.documentFields}
                      actionType={actionType}
                      viewFields={viewFields}
                      setDocFieldsId={setDocFieldsId}
                      setDocFieldsTitle={setDocFieldsTitle}
                      setDocFieldsDescription={setDocFieldsDescription}
                      setViewFields={setViewFields}
                      setActionType={setActionType}
                    />
                  ) : (
                    <div className="flex flex-col items-center self-center my-12">
                      <MdSettings className="w-16 h-16 animate__pulse" />
                      <p>Select Category or Sub Category to View Fields</p>
                    </div>
                  )}
                </div>
              )}
              {/* <!-- End of Right Side --> */}
            </div>
          </div>
        )}

        {/* modal for creating new categories or sub category */}
        {actionType !== "" && (
          <Modal
            backDrop={true}
            theme={Themes.default}
            close={(): void => {}}
            displayClose={false}
            backDropClose={false}
            title={
              <div className="flex items-center space-x-5">
                <div className="block pl-2 font-semibold text-xl self-start text-gray-700">
                  {/* <p className="text-base text-gray-500 font-medium">
                    You are{" "}
                    {actionType === "CREATE_SUB" || "CREATE_CAT"
                      ? "creating"
                      : "updating"}
                  </p> */}
                  <h2 className="uppercase text-back">
                    {actionType === "CREATE_SUB"
                      ? "New " + docCategory?.title
                      : actionType === "CREATE_CAT"
                      ? "New Category"
                      : actionType === "CREATE_FIELD"
                      ? "New Field"
                      : actionType === "UPDATE_SUB"
                      ? "Sub Category"
                      : actionType === "UPDATE_CAT"
                      ? "Category"
                      : actionType === "UPDATE_FIELD" && "Fields"}
                  </h2>
                </div>
              </div>
            }
            widthSizeClass={ModalSize.extraLarge}
            padding={{ title: true, body: false, footer: false }}
          >
            <div className="pb-8 w-full bg-gray-100">
              {(actionType === "CREATE_SUB" || actionType === "UPDATE_SUB") &&
                docCategory && (
                  <CreateSubDocument
                    thisCategory={docCategory}
                    subCategoryId={subCategoryId}
                    setSubCategoryId={setSubCategoryId}
                    setSubCategoryTitle={setSubCategoryTitle}
                    subCategoryTitle={subCategoryTitle}
                    setSubCategoryDescription={setSubCategoryDescription}
                    subCategoryDescription={subCategoryDescription}
                    setActionType={setActionType}
                    actionType={actionType}
                  />
                )}
              {(actionType === "CREATE_CAT" || actionType === "UPDATE_CAT") && (
                <CreateDocument
                  categoryId={categoryId}
                  thisCategory={docCategory}
                  categoryTitle={categoryTitle}
                  categoryDescription={categoryDescription}
                  categoryAttributes={categoryAttributes}
                  docFieldsTitle={docFieldsTitle}
                  docFieldsDescription={docFieldsDescription}
                  fieldList={fieldList}
                  actionType={actionType}
                  setCategoryId={setCategoryId}
                  setCategoryTitle={setCategoryTitle}
                  setCategoryDescription={setCategoryDescription}
                  setCategoryAttributes={setCategoryAttributes}
                  setDocFieldsTitle={setDocFieldsTitle}
                  setDocFieldsDescription={setDocFieldsDescription}
                  setFieldList={setFieldList}
                  setActionType={setActionType}
                />
              )}
              {(actionType === "CREATE_FIELD" ||
                actionType === "UPDATE_FIELD") &&
                docCategory &&
                document.documentCategory &&
                document.documentSubCategory && (
                  <CreateDocumentField
                    thisCategory={docCategory}
                    subCategoryId={subCategoryId}
                    docFieldsId={docFieldsId}
                    docFieldsTitle={docFieldsTitle}
                    docFieldsDescription={docFieldsDescription}
                    actionType={actionType}
                    documentCategory={document.documentCategory}
                    documentSubCategory={document.documentSubCategory}
                    viewFields={viewFields}
                    setDocFieldsId={setDocFieldsId}
                    setDocFieldsTitle={setDocFieldsTitle}
                    setDocFieldsDescription={setDocFieldsDescription}
                    setActionType={setActionType}
                  />
                )}
            </div>
          </Modal>
        )}
        {/* end modal for creating new categories or sub category */}
      </div>
    </Fragment>
  );
};

const mapStateToProps = ({
  document,
}: StoreState): {
  document: DocumentReducerInterface;
} => {
  return { document };
};
export const DocumentCategory = connect(mapStateToProps, {
  getDocumentCategory,
  getDocumentSubCategory,
  deleteDocumentCategory,
  getDocumentFields,
  PushPopupAlert,
})(Index);
